.line-chart {
  display: flex;
  flex-direction: column;
  padding: 15px;
  height: 57vh;
}

.line-canvas-container {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.line-chart-container {
  position: relative;
  display: flex;
  justify-content: flex-end;
  width: 100%;
  padding: 0 4%;
}

.days-container {
  display: flex;
}

.days-container-price {
  flex: 1;
  display: flex;
  align-items: center;
}
.days-container-price > h1 {
  background: var(--text-clip);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.days-container-price > h5 {
  margin-left: 3%;
  margin-bottom: 1%;
}

.day-item {
  font-size: 0.8rem;
  cursor: pointer;
  padding: 10px;
  background: var(--secondary-color);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.day-item:hover {
  background: var(--text-clip);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.day-active {
  background: var(--text-clip);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media only screen and (max-width: 1350px) {
  .line-canvas-container {
    width: 100%;
  }
  .line-chart {
    margin: 2vh 0;
  }
}

@media only screen and (max-width: 500px) {
  .line-chart {
    height: 100vh;
  }
  .line-chart-container {
    flex-direction: column;
    align-items: center;
  }
  .days-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  .days-container-price > h1 {
    font-size: 1.5rem;
  }
  .days-container-price > h5 {
    font-size: 0.8rem;
    margin-right: 5px;
  }
}
